import {get, post} from "@/utils/request";

let api = "";
if (process.env.NODE_ENV == "development") {
  api = "/api";
}

export const getWaterPower = () => get(api + "/equipData/waterPower"); // 
export const getTempHumidity = () => get(api + "/equipData/tempHumidity"); // 

export const getCountByMonth = () => get(api + "/api/visitor/countByMonth"); // 
export const getCountByYear = () => get(api + "/api/visitor/countByYear"); // 

export const getParkData = () => get(api + "/equipData/parkData"); // 

export const saveRecentThermal = (p) => post(api + "/api/temp/saveTemp", p); // 
export const queryRecentThermal = () => get(api + "/api/temp/queryRecentTemp"); // 


export const saveLight = params => get("http://172.16.12.232:8002/adjust/light?percent=" + params.percent + "&_=" + new Date().getTime()); // 

export const getWeather = () => get("http://wthrcdn.etouch.cn/weather_mini?city="); // 



